<template><undefined>
<h1>Rate 评分</h1>
<p>评分组件。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>对评价进行展示。<br>
对事物进行快速的评级操作。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-rate v-model=\&quot;star1\&quot; @change=\&quot;_change\&quot;&gt;&lt;/v-rate&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: function() {\n            return {\n                star1: 1\n            };\n        },\n        methods: {\n            _change(val) {\n                console.log('selected:' + val);\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-rate v-model="star1" @change="_change"></v-rate>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的用法。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"star1"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"_change"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-rate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">star1</span>: <span class="hljs-number">1</span>
            };
        },
        <span class="hljs-attr">methods</span>: {
            _change(val) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'selected:'</span> + val);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 基本
  最简单的用法。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"star1"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"_change"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-rate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">star1</span>: <span class="hljs-number">1</span>
            };
        },
        <span class="hljs-attr">methods</span>: {
            _change(val) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'selected:'</span> + val);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-rate allow-half :value=\&quot;2.5\&quot;&gt;&lt;/v-rate&gt;\n&lt;/template&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-rate allow-half="" :value="2.5"></v-rate>
</template>
</div>
                  <div slot="description"><h4 id="ban-xing"><a class="header-anchor" href="#ban-xing" aria-hidden="true">¶</a> 半星</h4>
<p>支持选中半星。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">allow-half</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">'2.5'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-rate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 半星
  支持选中半星。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">allow-half</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">'2.5'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-rate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-rate v-model=\&quot;star2\&quot; @change=\&quot;_change\&quot;&gt;\n        &lt;span class=\&quot;ant-rate-text\&quot;&gt;{{ star2 }}星&lt;/span&gt;\n    &lt;/v-rate&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n    export default {\n        data: function() {\n            return {\n                star2: 6\n            };\n        },\n        methods: {\n            _change(val) {\n                console.log('selected:' + val);\n            }\n        }\n    }\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-rate v-model="star2" @change="_change">
        <span class="ant-rate-text">{{ star2 }}星</span>
    </v-rate>
</template>


</div>
                  <div slot="description"><h4 id="wen-an-zhan-shi"><a class="header-anchor" href="#wen-an-zhan-shi" aria-hidden="true">¶</a> 文案展示</h4>
<p>给评分组件加上文案展示。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'star2'</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"_change"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'ant-rate-text'</span>&gt;</span><span>{{</span> star2 <span>}}</span>星<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-rate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">star2</span>: <span class="hljs-number">6</span>
            };
        },
        <span class="hljs-attr">methods</span>: {
            _change(val) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'selected:'</span> + val);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 文案展示
  给评分组件加上文案展示。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">'star2'</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"_change"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">class</span>=<span class="hljs-string">'ant-rate-text'</span>&gt;</span><span>{{</span> star2 <span>}}</span>星<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">v-rate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">star2</span>: <span class="hljs-number">6</span>
            };
        },
        <span class="hljs-attr">methods</span>: {
            _change(val) {
                <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'selected:'</span> + val);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-rate disabled :value=\&quot;2\&quot;&gt;\n&lt;/v-rate&gt;&lt;/template&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-rate disabled :value="2">
</v-rate></template>
</div>
                  <div slot="description"><h4 id="zhi-du"><a class="header-anchor" href="#zhi-du" aria-hidden="true">¶</a> 只读</h4>
<p>只读，无法进行鼠标交互。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">'2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-brate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 只读
  只读，无法进行鼠标交互。
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">v-rate</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">'2'</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-brate</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</demo-box>
<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="rate-props"><a class="header-anchor" href="#rate-props" aria-hidden="true">¶</a> Rate Props</h3>
<table class="table"><thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>count</td>
<td>star 总数</td>
<td>Number</td>
<td>5</td>
</tr>
<tr>
<td>value</td>
<td>当前值，如果输入值超过 count 会被强制转换成count值</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>allowHalf</td>
<td>是否允许半选</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>只读，无法进行交互</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="rate-events"><a class="header-anchor" href="#rate-events" aria-hidden="true">¶</a> Rate Events</h3>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>change</td>
<td>选择的值发生变化的时候触发</td>
<td>value</td>
</tr>
</tbody>
</table>
</undefined></template>

<script>
  export default {
      data: function() {
          return {
              star1: 1,
              star2: 6,
          };
      },
      methods: {
          _change(val) {
              console.log('selected:' + val);
          }
      }
  }
</script>


